//-- Sizing
$unit: 1.2rem !default;

// -- Fonts
$preferred-font: "Roboto", "Helvetica", "Arial", sans-serif !default;
$font-size: 1.6 * $unit !default;
$font-size-tiny: 1.2 * $unit !default;
$font-size-small: 1.4 * $unit !default;
$font-size-normal: $font-size !default;
$font-size-big: 1.8 * $unit !default;
$font-weight-thin: 300 !default;
$font-weight-normal: 400 !default;
$font-weight-semi-bold: 500 !default;
$font-weight-bold: 700 !default;
@import '../../../../app/styles/_base';

.zone{

	@extend %flexcontainer;
	width: 100%;
	height: 100%;
	background-color: rgb(85, 182, 251);
	opacity: 0.6;
	transition-property: opacity;
	transition-duration: 300ms;
	transition-timing-function: ease-in-out;
	cursor: pointer;
	overflow: hidden;
	position: absolute;
	left: 0;
	top: 0;

}

.prompt{

	@extend %flexitem;
	font-size: 3vw;
	align-self: center;
	text-align: center;
	color: #fff;
	pointer-events: none;
	z-index: 1;

}

.isdrag{

	opacity: 0.8;

}

.isblocked{

	cursor: initial;

}

