}

.rippler{

	width: 140px;
	height: 140px;
	border-radius: 50%;
	position: absolute;
	top: 50%;
	left: 50%;
	margin: -70px 0 0 -70px;
	z-index: 1999;
	visibility: hidden;

}

.ripple{
	
	visibility: visible;
	animation-name: rippler;
	animation-duration: 1s;
	animation-iteration-count: 1;
	animation-timing-function: ease-out;

}



@keyframes rippler {

	0% {
		transform: scale( 1 );
		// background-color: rgba( 140, 203, 247, 1 );
		background-color: rgba( 255,255,255, 0.6 );
	}

	100% {
		transform: scale( 30 );
		background-color: rgba( 255,255,255, 0 );
	}

}


