@import "~react-toolbox/lib/_colors"; 
 $theme-building: true;
//$color-primary: $palette-blue-grey-500 !default;
$color-primary: rgb(89, 148, 189) !default;
//$color-primary-dark: $palette-blue-grey-700 !default;
$color-primary-dark: rgb(58, 107, 142) !default;

/*$color-primary-dark: rgb(38, 46, 93);*/

$color-accent: $palette-red-500 !default;
$color-accent-dark: $palette-red-700 !default;
$color-primary-contrast: $color-dark-contrast !default;
$color-accent-contrast: $color-dark-contrast !default;


$color-text: $palette-grey-900 !default;

/* $button-primary-color-hover: rgba($palette-grey-300, .2) !default;
$button-accent-color-hover: rgba($palette-grey-300, .2) !default;

$ripple-duration: 800ms !default;
$ripple-final-opacity: .3 !default; */
/* $ripple-size: 15 * $unit !default; */  



//-- Sizing
$unit: 1.2rem !default;

// -- Fonts
$preferred-font: "Roboto", "Helvetica", "Arial", sans-serif !default;
$font-size: 1.6 * $unit !default;
$font-size-tiny: 1.2 * $unit !default;
$font-size-small: 1.4 * $unit !default;
$font-size-normal: $font-size !default;
$font-size-big: 1.8 * $unit !default;
$font-weight-thin: 300 !default;
$font-weight-normal: 400 !default;
$font-weight-semi-bold: 500 !default;
$font-weight-bold: 700 !default;
@import '../../../app/styles/_base';

$border-radius: 0.2rem;

.dialog{

	@extend %dialog;

}

.image{

	max-width: 100%;
	border-top-left-radius: $border-radius;
	border-top-right-radius: $border-radius;

}

.comment{

	//padding: $layout-gutter-width-sm;
	//color: $palette-grey-700;

}

.dialog[data-react-toolbox="dialog"]{

	&>[role="body"]{

		padding: 0;

	}

}

.btns{

	position: absolute;
	right: $layout-gutter-width-sm;

}

.btn{

	float: right;
	margin-left: $layout-gutter-width-sm;

}

.inner{

	padding: $layout-gutter-width-sm;
	padding-bottom: 50px;
	position: relative;
	color: #4c4c4c;


	p{

		margin: $layout-gutter-width-sm 0;

	}

}



